    updateDashboardInfo () {
      let dashboardInfo = JSON.parse(sessionStorage.getItem('super.manage.dashboard.info'))
      if (dashboardInfo && dashboardInfo.hasOwnProperty('noOpenGame')) {
        // 不開放遊戲設定(管端列表內容，前端是使用API回傳值綁定表格值，所以表格內容遊戲開關應由後端判斷)
        // 註明：「BingoStar」和「皇家」若不開放，先註解 list 額度欄位(假資料關係)
        for (let i in dashboardInfo.noOpenGame) {
          this.closeGameType(dashboardInfo.noOpenGame[i])
        }
      }
    },
    // 不開放遊戲
    closeGameType (type) {
      switch (type) {
        // 關閉BingoStar
        case 's16':
          this.reportItems.bgTypeItems.show = false
          this.reportItems.bgGameTypeItems.show = false
          this.reportItems.bgPlayTypeItems.show = false
          break
        // 關閉Super彩球
        case 's17':
          this.reportItems.ltTypeItems.show = false
          this.reportItems.ltGameTypeItems.show = false
          this.reportItems.ltPlayTypeItems.show = false
          break
        // 關閉皇家視訊
        case 's18':
          this.reportItems.ryTypeItems.show = false
          this.reportItems.ryGameTypeItems.show = false
          this.reportItems.ryPlayTypeItems.show = false
          break
      }
    },
    // 初始語系 @看看能不能寫繼承@
    // initLanguage () {}
  },
  //BEGIN--生命週期
  beforeCreate: function() {
    //實體初始化
  },
  created: function() {
    //實體建立完成。資料 $data 已可取得，但 $el 屬性還未被建立。
    this.src = this.$options.__file ;
  },
  beforeMount: function() {
    //執行元素掛載之前。
  },
  mounted: function() {
    // 顯示切換按鈕
    this.showChangeBtn = !this.$store.state.app.drawer
    // 越南文語系且為手機視窗狀態
    this.viMobileStatus = this.$store.state.lang === 'vi' && window.innerWidth < 630

    this.$nextTick(() => {
      window.addEventListener('resize', this.onResize)
    })

    this.initLanguage()

    // 移除搜尋後的層級
    sessionStorage.removeItem('report.search.level')
    // 移除搜尋帳號
    sessionStorage.removeItem('report.search.account')

    this.updateDashboardInfo()
  },
  beforeUpdate: function() {
    //當資料變化時被呼叫，還不會描繪 View。
  },
  updated: function() {
    //當資料變化時被呼叫，還不會描繪 View。
  },
  beforeDestroy: function() {
    window.removeEventListener('resize', this.onResize)
  },
  destroyed: function() {
    // 清除 timer
    clearInterval(this.updateTimer)
  }
  //END--生命週期
};
</script>

